import axios from 'axios';

require('dotenv').config();
const state = {
  data_user: {
    page: 1,
    per_page: 10,
    list_data: [],
  },
  state_user: 'all',
  state_find: {
    error: false,
    success: false,
    error_message: '',
    success_message: '',
  },
};

const mutations = {
  SET_STATE_DATA(state, _source) {
    state.data_user.page = _source.page;
    state.data_user.per_page = _source.per_page;
    state.data_user.list_data = _source.list_data;
  },
  SET_STATE_LIST(state, _source) {
    state.data_user.list_data = _source;
  },
  SET_STATE_FIND(state, _source) {
    state.data_user.per_page = _source;
  },
  SET_PAGE(state, _source) {
    state.data_user.list_data = _source;
  },
  SET_STATE(state, _source) {
    state.state_user = _source;
  },
  SET_STATE_FIND(state, _source) {
    state.state_find.error = _source.error;
    state.state_find.success = _source.success;
    state.state_find.error_message = _source.error_message;
    state.state_find.success_message = _source.success_message;
  },
};

const actions = {
  async changeStateUser({ commit }, _data) {
    commit('SET_STATE', _data);
  },
  async getListUser({ commit, state }) {
    const state_find = {
      error: false,
      success: false,
      error_message: '',
      success_message: '',
    };
    commit('SET_STATE_FIND', state_find);

    console.log('running');
    try {
      // Ambil token JWT dari localStorage
      const jwtToken = localStorage.getItem('jwtToken'); // Pastikan nama item sesuai dengan yang Anda gunakan

      if (!jwtToken) {
        // Handle jika token tidak ada di localStorage
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`https://api.specta.app/v1/private/admin/users/list?page=${state.data_user.page}&per_page=20&state=${state.state_user}`, { headers });
      commit('SET_STATE_DATA', response.data.data);
      return response.data.data;
    } catch (error) {
      console.log('Error:', error.response);
      return error.response;
    }
  },
  async getPrevData({ commit, state }) {
    if (state.data_user.page == 1) {
      return;
    }
    const state_find = {
      error: false,
      success: false,
      error_message: '',
      success_message: '',
    };
    commit('SET_STATE_FIND', state_find);

    console.log('running');
    try {
      // Ambil token JWT dari localStorage
      const jwtToken = localStorage.getItem('jwtToken'); // Pastikan nama item sesuai dengan yang Anda gunakan

      if (!jwtToken) {
        // Handle jika token tidak ada di localStorage
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`https://api.specta.app/v1/private/admin/users/list?page=${state.data_user.page - 1}&per_page=20&state=${state.state_user}`, { headers });
      commit('SET_STATE_DATA', response.data.data);
      return response.data.data;
    } catch (error) {
      console.log('Error:', error.response);
      return error.response;
    }
  },
  async getNextData({ commit, state }) {
    const state_find = {
      error: false,
      success: false,
      error_message: '',
      success_message: '',
    };
    commit('SET_STATE_FIND', state_find);

    console.log('running');
    try {
      // Ambil token JWT dari localStorage
      const jwtToken = localStorage.getItem('jwtToken'); // Pastikan nama item sesuai dengan yang Anda gunakan

      if (!jwtToken) {
        // Handle jika token tidak ada di localStorage
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };

      const response = await axios.get(`https://api.specta.app/v1/private/admin/users/list?page=${state.data_user.page + 1}&per_page=20&state=${state.state_user}`, { headers });
      commit('SET_STATE_DATA', response.data.data);
      return response.data.data;
    } catch (error) {
      console.log('Error:', error.response);
      return error.response;
    }
  },
  async findUsers({ commit, state }, _email) {
    const state_find = {
      error: false,
      success: false,
      error_message: '',
      success_message: '',
    };
    commit('SET_STATE_FIND', state_find);
    try {
      // Ambil token JWT dari localStorage
      const jwtToken = localStorage.getItem('jwtToken'); // Pastikan nama item sesuai dengan yang Anda gunakan

      if (!jwtToken) {
        // Handle jika token tidak ada di localStorage
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`https://api.specta.app/v1/private/admin/users/find_user?email_find=${_email}`, { headers });
      commit('SET_STATE_LIST', response.data.list_data);
      return response.data;
    } catch (error) {
      console.log('Error:', error);
      const state_find = {
        error: true,
        success: false,
        error_message: '',
        success_message: '',
      };
      commit('SET_STATE_FIND', state_find);
      return error.response;
    }
  },
};
const getters = {
  userData: (state) => state.data_user,
  stateData: (state) => state.state_find,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
