// store/presale.js

import axios from 'axios';

require('dotenv').config();

const state = {
  tradeData: {
    currency: '',
    amount: 0,
    paid: 0,
    hash: '',
    address: 0,
    presale_address: 0,
    id: 0,
  },
  state_badge: {
    error: false,
    success: false,
    error_message: '',
    success_message: '',
  },
  state_auth: {
    error: false,
    success: false,
    error_message: '',
    success_message: '',
  },
};

const mutations = {
  SET_STATE_AUTH(state, _source) {
    state.state_auth.error = _source.error;
    state.state_auth.success = _source.success;
    state.state_auth.error_message = _source.error_message;
    state.state_auth.success_message = _source.success_message;
  },
};

const actions = {
  async signin({ commit }, _source) {
    try {
      const response = await axios.post('https://api.specta.app/v1/auth/signin', _source);
      console.log('Response:', response.data);

      // Save the token to localStorage
      localStorage.setItem('jwtToken', response.data.token);

      return response.data;
    } catch (error) {
      console.log('Error:', error.response.data);

      const data_send = {
        error: true,
        success: false,
        error_message: error.response.data.message,
      };

      commit('SET_STATE_AUTH', data_send);

      return error.response.data;
    }
  },
};
const getters = {
  authData: (state) => state.state_auth,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
