import axios from 'axios';
require('dotenv').config();
const BASE_URL = 'https://api.specta.app/v1/private/admin/trade/';

const state = {
  data_settings: {},
  input_data: false,
  state_settings: {
    error: false,
    success: false,
    error_message: '',
    success_message: '',
  },
};

const mutations = {
  SET_DATA(state, _source) {
    state.data_settings = _source;
  },
  SET_INPUT(state, _source) {
    state.input_data = _source;
  },
  SET_PAGE(state, _source) {
    state.current_page = _source;
  },
  SET_STATS(state, _source) {
    state.state_settings.error = _source.error;
    state.state_settings.success = _source.success;
    state.state_settings.error_message = _source.error_message;
    state.state_settings.success_message = _source.success_message;
  },
};

const actions = {
  async getSettings({ commit }) {
    console.log('trade settings');
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}settings`, { headers });
      commit('SET_DATA', response.data.data);
      return response.data;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  async setSettings({ commit }) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.post(`${BASE_URL}settings`, state.data_settings, { headers });
      if (response) {
        commit('SET_INPUT', false);
        const state_settings = {
          error: false,
          success: true,
          error_message: '',
          success_message: `update has been successful`,
        };
        commit('SET_STATS', state_settings);
      }

      return;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  changeInput({ commit }, _data) {
    commit('SET_INPUT', _data);
  },
};
const getters = {
  tradeData: (state) => state.data_settings,
  stateData: (state) => state.state_settings,
  pageData: (state) => state.current_page,
  inputState: (state) => state.input_data,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
