import axios from 'axios';
require('dotenv').config();
const BASE_URL = 'https://api.specta.app/v1/private/admin/settings/';

const state = {
  data_reff_settings: [],
  state_settings: {
    error: false,
    success: false,
    error_message: '',
    success_message: '',
  },
};

const mutations = {
  SET_SETTINGS(state, _source) {
    state.data_reff_settings = _source;
  },
  SET_STATS(state, _source) {
    state.state_settings.error = _source.error;
    state.state_settings.success = _source.success;
    state.state_settings.error_message = _source.error_message;
    state.state_settings.success_message = _source.success_message;
  },
};

const actions = {
  async getSettings({ commit }) {
    console.log('running');
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}reff_earn`, { headers });
      commit('SET_SETTINGS', response.data.data);
      return response.data;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  async updateSettings({ commit, context }) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.post(`${BASE_URL}reff_earn`, { data_settings: state.data_reff_settings }, { headers });
      const state_settings = {
        error: false,
        success: true,
        error_message: '',
        success_message: response.data.message,
      };

      commit('SET_STATS', state_settings);
      await context.dispatch('getSettings');
      return response;
    } catch (error) {
      console.log('Error:', error.response.data.message);
      const state_settings = {
        error: true,
        success: false,
        error_message: error.response.data.message,
        success_message: '',
      };

      commit('SET_STATS', state_settings);

      return error;
    }
  },
  async newSettings({ commit, state }) {
    let data = { id: state.data_reff_settings[state.data_reff_settings.length - 1].id + 1, value: '0' };
    state.data_reff_settings.push(data);
    console.log('running');
  },
  async delSettings({ commit, state }) {
    if (state.data_reff_settings.length == 1) {
      const state_settings = {
        error: true,
        success: false,
        error_message: "You can't delete the settings",
        success_message: '',
      };
      commit('SET_STATS', state_settings);
      return;
    }
    state.data_reff_settings.pop();
  },
};
const getters = {
  settingsReffData: (state) => state.data_reff_settings,
  settingsReffData: (state) => state.data_reff_settings,
  stateData: (state) => state.state_settings,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
