import axios from 'axios';
require('dotenv').config();
const BASE_URL = process.env.BASE_URL;

const state = {
  dashboard_data: {
    user_count: 100,
    total_staked: null,
    nextReward: null,
    adminBalance: 21571235023.119,
    latestUser: [],
    pending_withdraw: [],
  },
  state: {
    error: false,
    success: false,
    error_message: '',
    success_message: '',
  },
};

const mutations = {
  SET_STATE_DATA(state, _source) {
    state.dashboard_data.user_count = _source.user_count;
    state.dashboard_data.total_staked = _source.total_staked;
    state.dashboard_data.nextReward = _source.nextReward;
    state.dashboard_data.adminBalance = _source.adminBalance;
    state.dashboard_data.user_count = _source.user_count;
    state.dashboard_data.latestUser = _source.latestUser;
    state.dashboard_data.pending_withdraw = _source.pending_withdraw;
  },
};

const actions = {
  async getDashboard({ commit }) {
    console.log('running');
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get('https://api.specta.app/v1/private/admin/dashboard', { headers });
      commit('SET_STATE_DATA', response.data.data);
      return response.data.data;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
};
const getters = {
  dashboardData: (state) => state.dashboard_data,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
