import axios from 'axios';
require('dotenv').config();
const BASE_URL = 'https://api.specta.app/v1/private/admin/stake/';

const state = {
  list_data: [{}],
  current_page: 1,
  state_settings: {
    error: false,
    success: false,
    error_message: '',
    success_message: '',
  },
};

const mutations = {
  SET_LIST(state, _source) {
    state.list_data = _source;
  },
  SET_PAGE(state, _source) {
    state.current_page = _source;
  },
  SET_STATS(state, _source) {
    state.state_settings.error = _source.error;
    state.state_settings.success = _source.success;
    state.state_settings.error_message = _source.error_message;
    state.state_settings.success_message = _source.success_message;
  },
};

const actions = {
  async getList({ commit }) {
    console.log('stake list');
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}list?page=1&per_page=10`, { headers });
      commit('SET_LIST', response.data.data.list_data);
      return response.data;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  async getListFree({ commit }) {
    console.log('stake list');
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}list?page=1&per_page=10&state=free`, { headers });
      commit('SET_LIST', response.data.data.list_data);
      return response.data;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  async getListReal({ commit }) {
    console.log('stake list');
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}list?page=1&per_page=10&state=real`, { headers });
      commit('SET_LIST', response.data.data.list_data);
      return response.data;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  async getPrevData({ commit }) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };

      const response = await axios.get(`${BASE_URL}list?page=${state.current_page + 1}&per_page=10`, { headers });
      commit('SET_LIST', response.data.data.list_data);
      commit('SET_PAGE', state.current_page + 1);
      return response.data;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  async getNextData({ commit }) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };

      const response = await axios.get(`${BASE_URL}list?page=${state.current_page + 1}&per_page=10`, { headers });
      commit('SET_LIST', response.data.data.list_data);
      commit('SET_PAGE', state.current_page + 1);
      return response.data;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  resetState({ commit }) {
    const state_settings = {
      error: false,
      success: false,
      error_message: '',
      success_message: '',
    };
    commit('SET_STATS', state_settings);
  },
  async newAdjust({ commit, context }, _data) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.post(`${BASE_URL}new`, _data, { headers });
      const state_settings = {
        error: false,
        success: true,
        error_message: '',
        success_message: response.data.message,
      };

      commit('SET_STATS', state_settings);
      await context.dispatch('getList');
    } catch (error) {
      console.log('Error:', error.response.data.message);
      const state_settings = {
        error: true,
        success: false,
        error_message: error.response.data.message,
        success_message: '',
      };

      commit('SET_STATS', state_settings);

      return error;
    }
  },
};
const getters = {
  stakeData: (state) => state.list_data,
  stateData: (state) => state.state_settings,
  pageData: (state) => state.current_page,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
