import axios from 'axios';
require('dotenv').config();
const BASE_URL = 'https://api.specta.app/v1/private/admin/users/stake_detail/';

const state = {
  list_data: {},
  current_page: 1,
  id_stake: 1,
  state_tx: 'all',
  state_settings: {
    error: false,
    success: false,
    error_message: '',
    success_message: '',
  },
};

const mutations = {
  SET_LIST(state, _source) {
    state.list_data = _source;
  },
  SET_STATS(state, _source) {
    state.state_settings.error = _source.error;
    state.state_settings.success = _source.success;
    state.state_settings.error_message = _source.error_message;
    state.state_settings.success_message = _source.success_message;
  },
  SET_ID(state, _source) {
    state.id_stake = _source;
  },
};

const actions = {
  async getList({ commit, state }) {
    console.log('Run');
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}${state.id_stake}`, { headers });
      commit('SET_LIST', response.data.data.data);
      return response.data;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  resetState({ commit }) {
    const state_settings = {
      error: false,
      success: false,
      error_message: '',
      success_message: '',
    };
    commit('SET_STATS', state_settings);
  },
  changeType({ commit }, _data) {
    commit('SET_TX', _data);
  },

  changeId({ commit }, _data) {
    commit('SET_ID', _data);
  },
};
const getters = {
  stakeData: (state) => state.list_data,
  stateData: (state) => state.state_settings,
  idData: (state) => state.id_stake,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
