import axios from 'axios';
require('dotenv').config();
const BASE_URL = 'https://api.specta.app/v1/private/admin/users/';

const state = {
  user_data: {
    details_user: {
      id: 0,
      email: '',
      role: 'user',
      state: 'active',
      is_claimed: true,
      telegram: '',
      binance_pay_id: 0,
      binance_bep20_address: '',
      reff_code: '',
      created_at: '',
      balance: {
        balance: '0.0000',
        staked_balance: '0.0000',
        airdrop_balance: '0.0000',
        pending_balance: '0.0000',
        deposit_address: '',
      },
    },
    total_earn: '',
    total_reff: 0,
    total_buy: 0,
    total_sell: 0,
    total_earn_reff: 0,
  },
  user_id: 1,
  transaction_type: 'all',
  state_settings: {
    error: false,
    success: false,
    error_message: '',
    success_message: '',
  },
};

const mutations = {
  SET_USER(state, _source) {
    state.user_data = _source;
  },
  SET_ID(state, _source) {
    state.user_id = _source;
  },
  SET_TYPE(state, _source) {
    state.transaction_type = _source;
  },
  SET_STATS(state, _source) {
    state.state_settings.error = _source.error;
    state.state_settings.success = _source.success;
    state.state_settings.error_message = _source.error_message;
    state.state_settings.success_message = _source.success_message;
  },
};

const actions = {
  async getUser({ commit }) {
    console.log('stake list');
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}details/${state.user_id}`, { headers });
      console.log(response);
      commit('SET_USER', response.data.data);
      return;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  resetState({ commit }) {
    const state_settings = {
      error: false,
      success: false,
      error_message: '',
      success_message: '',
    };
    commit('SET_STATS', state_settings);
  },
  changeType({ commit }, _data) {
    commit('SET_TYPE', _data);
  },
  updateId({ commit }, _data) {
    commit('SET_ID', _data);
  },
};
const getters = {
  userData: (state) => state.user_data,
  stateData: (state) => state.state_settings,
  pageData: (state) => state.current_page,
  typeData: (state) => state.transaction_type,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
