import axios from 'axios';
require('dotenv').config();
const BASE_URL = 'https://api.specta.app/v1/private/admin/users/';

const state = {
  list_data: {},
  current_page: 1,
  user_id: 1,
  level: 'all',
  state_settings: {
    error: false,
    success: false,
    error_message: '',
    success_message: '',
  },
};

const mutations = {
  SET_LIST(state, _source) {
    state.list_data = _source;
  },
  SET_PAGE(state, _source) {
    state.current_page = _source;
  },
  SET_TX(state, _source) {
    state.level = _source;
  },
  SET_STATS(state, _source) {
    state.state_settings.error = _source.error;
    state.state_settings.success = _source.success;
    state.state_settings.error_message = _source.error_message;
    state.state_settings.success_message = _source.success_message;
  },
  SET_ID(state, _source) {
    state.user_id = _source;
  },
};

const actions = {
  async getList({ commit }) {
    console.log('returning');
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}reff_list?page=1&per_page=20&id_user=${state.user_id}&level=${state.level}`, { headers });
      commit('SET_LIST', response.data.data.list_data);
      return response.data;
    } catch (error) {
      console.log('Error:', error);
      return error.response.data;
    }
  },
  async getPrevData({ commit }) {
    if (state.current_page == 1) {
      return;
    }
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}reff_list?page=${state.current_page - 1}&per_page=20&id_user=${state.user_id}&level=${state.level}`, { headers });
      commit('SET_LIST', response.data.data.list_data);
      commit('SET_PAGE', state.current_page - 1);
      return response.data;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  async getNextData({ commit }) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}reff_list?page=${state.current_page + 1}&per_page=20&id_user=${state.user_id}&level=${state.level}`, { headers });
      commit('SET_LIST', response.data.data.list_data);
      commit('SET_PAGE', state.current_page + 1);
      return response.data;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  resetState({ commit }) {
    const state_settings = {
      error: false,
      success: false,
      error_message: '',
      success_message: '',
    };
    commit('SET_STATS', state_settings);
  },
  changeLevel({ commit }, _data) {
    commit('SET_TX', _data);
  },
  changePage({ commit }, _data) {
    commit('SET_PAGE', _data);
  },
  userId({ commit }, _data) {
    commit('SET_ID', _data);
  },
};
const getters = {
  listData: (state) => state.list_data,
  stateData: (state) => state.state_settings,
  pageData: (state) => state.current_page,
  levelData: (state) => state.level,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
